<template>
  <b-row class="hp-authentication-page">
    <left-item />

    <b-col cols="12" lg="6" class="py-sm-64 py-lg-0">
      <b-row align-v="center" align-h="center" class="h-100 mx-4 mx-sm-n32">
        <b-col cols="12" md="9" xl="7" class="col-xxxl-5 px-8 px-sm-0 pt-24 pb-48">
          <b-alert v-if="authData.error" show variant="danger">
            <h4 class="alert-heading">Something Wrong!!</h4>
            <p>{{ authData.error_message }}</p>
          </b-alert>
          <h1 class="mb-0 mb-sm-24">Login</h1>
          <!-- <h1 class="mb-0 mb-sm-24">Login</h1> -->

          <p class="mt-sm-8 mt-sm-0 text-black-60">Welcome back, please login to your account.</p>

          <b-form class="mt-16 mt-sm-32 mb-8">
            <b-form-group label="email :" label-for="loginUsername" class="mb-16">
              <b-form-input id="loginUsername" type="text" v-model="data_user.email"></b-form-input>
            </b-form-group>

            <b-form-group label="Password :" label-for="loginPassword" class="mb-16">
              <b-form-input id="loginPassword" type="password" v-model="data_user.password"></b-form-input>
            </b-form-group>

            <b-row align-v="center" align-h="between" class="mb-16">
              <b-col class="hp-flex-none w-auto">
                <b-form-checkbox>Remember me</b-form-checkbox>
              </b-col>
            </b-row>
          </b-form>
          <b-button to="/" type="submit" variant="primary" class="w-100" @click="signIn()"> Sign in </b-button>

          <footer-item />
        </b-col>
      </b-row>
    </b-col>
  </b-row>
</template>

<script>
import { BRow, BCol, BButton, BLink, BForm, BFormGroup, BFormInput, BFormCheckbox, BAlert } from 'bootstrap-vue';

import LeftItem from '../LeftItem.vue';
import FooterItem from '../FooterItem.vue';

import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  data() {
    return {
      data_user: { email: '', password: '' },
    };
  },
  computed: {
    ...mapGetters('spectaStore', ['authData']),
  },
  components: {
    BRow,
    BCol,
    BButton,
    BLink,
    BForm,
    BFormGroup,
    BFormInput,
    BFormCheckbox,
    LeftItem,
    FooterItem,
    BAlert,
  },
  methods: {
    ...mapActions('spectaStore', ['signin']),
    async signIn() {
      const res = await this.signin(this.data_user);
      if (res) {
        this.$router.push({ name: 'Dashboard' });
      }
    },
  },
};
</script>
